import Layout from '../components/Layout'

export default ({ statusCode }) => (
  <Layout title={'404'}>
    {stutusCode ? `Coud not load your user data: Status code ${statusCode}`: null}
    <p>Couldn't get that page, sorry!</p>
    <style jsx>{`
      h1, p{
        color: red;
      }
    `}</style>
  </Layout>
)